import React, { useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { useQuery } from 'react-query';
import { Spinner } from '../../components/Spinner/Spinner';

import './Event.scss';
import {IEvent} from "../../typings";

const Event = () => {
    const [event, setEvent] = useState<IEvent>({} as IEvent);

    const { id } = useParams();

    useQuery(`event-${id}`, async () => {
        const response = await fetch(
            `https://cors-anywhere.herokuapp.com/api.smarkets.com/v3/events/${id}`,
            {
                headers: {
                    'Content-Type': 'application/json',
                },
            }
        );
        const { events } = await response.json();
        const event = events[0];

        setEvent({
            id: event.id,
            name: event.name,
            startTime: event.start_datetime,
            shortName: event.short_name,
        });
    });
    const a = event.startTime && new Date(event.startTime);

    return (
        <div className="EventPage">
            <Link to={'/'}>Back to the list</Link>
            {event.id ? (
                <div className="CurrentEvent">
                    <h3>
                        {event.name} ({event.shortName})
                    </h3>
                    <p>Start time: {a.toLocaleString()}</p>
                </div>
            ) : (
                <Spinner />
            )}
        </div>
    );
};

export default Event;
